import React, { useContext, useEffect, useState } from 'react';
import { withRouter } from "react-router-dom";
import { Context } from '../../Context';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Title from '../../pages/dashboard/Title';

const useStyles = makeStyles(theme => ({


    flex: {
        // display: 'flex',
        // // justifyContent: ' space-between',
        // fontSize:18,
        // width:'100%'
    },
    p1:{
        display:'inline-block',
        width:'25%',
        margin:'5 0',
        padding:0,
        fontSize:18,
      
    },
    p2:{
        display:'inline-block',
        width:'50%',
        margin:'5 0',
        padding:0,
        fontSize:18,
      
    }
    
}));

function App(props) {
    const { state, dispatch } = useContext(Context);
    const [data, setData] = useState({});
    const uid = props.match.params.uid;
    const classes = useStyles();

    async function getInfo(uid) {
        const res = await fetch(`${state.host}/userInfo?uid=${uid}`, { credentials: 'include', method: 'get' });
        const result = await res.json();
        const data = result.data;
        // setUserList(ul);
        setData(data);
        console.log(data);
    }

    useEffect(() => {
        getInfo(uid);
    }, [])
    // {data.certNumber.replace(data.certNumber.substring(1,17),'***************')}
    return (
        <>
           <Title>用户信息</Title>
            <Divider />
            <div className={classes.flex}>
                <p className={classes.p1}>用户姓名:{data.name} </p>
                <p className={classes.p1}> 联系方式:{data.phone}</p>
                <p className={classes.p2}> 身份证:{data.certNumber&&data.certNumber.replace(data.certNumber.substring(1,17),'***************')}</p>

            </div>
            <div className={classes.flex}>
                <p className={classes.p1}>注册日期:{data.createTime} </p>
                <p className={classes.p1}> 性别:{data.sex}</p>
                <p className={classes.p2}> 联系地址:{data.address}</p>

            </div>
        </>
    );
}

export default withRouter(App);