import React from 'react';
import DateFnsUtils from '@date-io/date-fns';
import TimeChart from '../../component/TimeChart'
import Title from '../../pages/dashboard/Title';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import RemoveIcon from '@material-ui/icons/Remove';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}
const useStyles = makeStyles(theme => ({
  timeBar: {
    display:'flex',
    backgroundColor: theme.palette.background.paper,
    width: ' 100%',
    margin:'20px 0',
    justifyContent: 'center',
    alignItems:'center'
  },
  mt:{
    marginTop:26,
  }

}));

export default function Chart() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);
  const [selectedDate, setSelectedDate] = React.useState();

 const[pickDay,setPickDay] =React.useState(0);



  const handleChange = (event, newValue) => {

    setValue(newValue);
  };

  const handleChangeIndex = index => {
    setValue(index);
  };
  const handleDateChange = date => {
    setSelectedDate(date);
  };

  return (
    <React.Fragment>
      <Title>用户问诊记录</Title>
      <div className={classes.timeBar}>

        <MuiPickersUtilsProvider utils={DateFnsUtils}>

          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            margin="normal"
            id="date-picker-inline"
            label="起始时间"
            value={selectedDate}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
          />
          <RemoveIcon className={classes.mt} />
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            margin="normal"
            id="date-picker-inline"
            label="结束时间"
            value={selectedDate}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
          />
        </MuiPickersUtilsProvider>
        <ButtonGroup size="small" aria-label="outlined primary button group" className={classes.mt}>
       { pickDay=== 7?<Button   color="secondary"  onClick={()=>{setPickDay(7);}}>七天</Button> :
       <Button  onClick={()=>{setPickDay(7)}}>七天</Button>
       } 
       { pickDay=== 30?<Button  color="secondary"  onClick={()=>{setPickDay(30)}}>一个月</Button> :
       <Button  onClick={()=>{setPickDay(30)}}>一个月</Button>
       }
         { pickDay=== 90?<Button  color="secondary"  onClick={()=>{setPickDay(90)}}>三个月</Button> :
       <Button  onClick={()=>{setPickDay(90)}}>三个月</Button>
       }
         { pickDay=== 180?<Button  color="secondary"  onClick={()=>{setPickDay(180)}}>半年</Button> :
       <Button  onClick={()=>{setPickDay(180)}}>半年</Button>
       }
       { pickDay=== 360?<Button  color="secondary"  onClick={()=>{setPickDay(360)}}>一年</Button> :
       <Button  onClick={()=>{setPickDay(360)}}>一年</Button>
       }
        <Button>全部</Button>
      </ButtonGroup>
      &nbsp;&nbsp;
      <Button size="small" variant="contained" color="primary" className={classes.mt} disableElevation>
        查看
      </Button>
      </div>
      <TimeChart />



    </React.Fragment>
  );
}