import React, { useContext, useEffect, useState } from 'react';
import { Context } from '../../../Context';
import MaterialTable from 'material-table';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { withRouter } from "react-router-dom";
import AcUnitIcon from '@material-ui/icons/AcUnit';

import {
    columnsArr,
    optionsSetting,
    localizationConfig,
    tableIcons
} from './materialTableConfig'


function App(props) {
    const { state, dispatch } = useContext(Context);
    const [OK, setOK] = useState(true);
    const [userList, setUserList] = useState();
    async function getUserList() {
        const res = await fetch(`${state.host}/userList?currentPage=1&pageSize=5`, {
            credentials: 'include', method: 'get', headers: {
                "Content-type": "application/json"
            },
        });
        const result = await res.json();
        let pageSize = result.data.total;
        const res2 = await fetch(`${state.host}/userList?pageSize=${pageSize}`, {
            credentials: 'include', method: 'get', headers: {
                "Content-type": "application/json"
            },
        });
        const result2 = await res2.json();
        let data = result2.data.records;
        setUserList(data);
        console.log(data);
    }
    useEffect(() => {
        dispatch({
            type: 'setNowLocal',
            value: '用户管理 - 普通用户'
        });


        getUserList();

    }, []);




    return (
        <MaterialTable
            title="用户列表"
            columns={columnsArr}
            data={userList}
            icons={tableIcons}
            options={optionsSetting}
            // 显示设置
            localization={localizationConfig}
            actions={[
                {
                    icon: VisibilityIcon,
                    tooltip: '查看用户',
                    onClick: (event, rowData) => props.history.push(`/home/personInfo/${rowData.uid}`
                    )
                },
                {
                    icon: AcUnitIcon,
                    tooltip: '冻结用户',
                    onClick: (event, rowData) => {
                        const post = {
                            uid:rowData.uid,
                        }
                        
                           fetch(`${state.host}/blockUser`, {
                                // post提交
                                method: "POST",
                                headers: {
                                    "Content-type": "application/json"
                                },
                                body: JSON.stringify(post)//把提交的内容转字符串
                  
                            }).then(res =>
                            res.json()
                        ).then(
                            res => {
                                setOK(!OK);
                                console.log(res);

                            }
                        ).catch((err) => {
                            console.log(err);
                        })


                    }
                }
            ]}
            editable={{
                onRowAdd: newData =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();
                            setUserList(prevState => {
                                // const data = [...prevState.data];
                                // data.push(newData);
                                // return { ...prevState, data };
                            });
                        }, 600);
                    }),

            }}

        />
    );
}

export default withRouter(App);